<template>
  <div class="login">
    <div class="login-poster">
        <div class="login-info">
            <img src="@/assets/images/login.jpg">
        </div>
    </div>
    <van-cell-group>
        <van-field
            v-model="username"
            required
            label=""
            placeholder=""
        />
        <van-field
            v-model="password"
            type="password"
            label=""
            placeholder=""
            required
        />
    </van-cell-group>
    <div class="button">
        <van-button type="default" @click="login"></van-button>
        <van-button type="info" @click="register"></van-button>
    </div>
    <div class="tip">
        <p></p>
    </div>
  </div>
</template>

<script>
import { CellGroup, Field, Button, Dialog } from 'vant';
import AsyncValidator from 'async-validator'

export default {
    components: {
        [Field.name]: Field,
        [CellGroup.name]: CellGroup,
        [Button.name]: Button,
    },
    data () {
        return {
            username: '',
            password: '',
        }
    },
    methods: {
        savaUser (res) {
            localStorage.setItem('token', res.sessionToken)
            localStorage.setItem('username', res.username)
            this.$store.commit('setState', {key: 'token', value: res.sessionToken})
            this.$store.commit('setState', {key: 'username', value: res.username})
        },
        login () {
            this.$axios.get(
                '/login',
                {params:{ 
                    'username': this.username,
                    'password': this.password
                }}
            )
            .then(res => {
                this.savaUser(res)
                Dialog({
                    message: ''
                }).then(() => {
                    this.$router.push('/index')
                })
            })
            .catch(err => {
                switch (err.code) {
                    case 210:
                        Dialog({
                            message: ''
                        })                
                        break
                    case 211:
                        Dialog.confirm({
                            message: ''
                        }).then(() => {
                            this.register()
                        })
                        break
                    default:
                        Dialog({
                            message: ''
                        }) 
                        break
                }
            })
        },
        register () {
            this.$axios.post(
                '/users',
                {
                    'username': this.username,
                    'password': this.password,
                }
            )
            .then(res => {
                this.savaUser(res)
                Dialog({
                    message: ''
                }).then(() => {
                    this.$router.push('/index')
                })
            })
            .catch(err => {
                switch (err.code) {
                    case 217:
                        Dialog({
                            message: ''
                        })
                        break;
                    case 218:
                        Dialog({
                            message: ''
                        })
                        break;
                    case 202:
                        Dialog({
                            message: ''
                        })
                        break;
                    default:
                        Dialog({
                            message: ''
                        })
                        break;
                }
            })
        }
    }
};
</script>

<style lang="less" scoped>
.login {
    &-poster {
        width: 100%;
        height: 53vw;
        display: flex;
        align-items: center;
        justify-content: center;
        background: url('~@/assets/images/background.jpg');
        background-size: cover;
    }
    &-info {
        width: 100px;
        height: 100px;
        text-align: center;
        img{
            width: 100%;
            height: 100%;
            border-radius: 50%;
            margin-bottom: 5px;
        }
    }
}
.van-cell-group {
    margin-top: 20px;
}
.tip{
    color: red;
    text-align: center;
    p{
        display: inline-block;
    }
}
.button{
    margin: 50px 0;
    text-align: center;
    .van-button{
        width: 100px;
        &:first-child{
            margin-right: 40px
        }
    }
}
</style>